/**
 * PptxGenJS  PPTX 
 * #message 
 */
function createPptFromMessage(): void {
  // 1. #message 
  const messageElement = document.getElementById("message") as HTMLElement | null;
  const messageText = messageElement ? messageElement.innerText : "";

  // 2. PptxGenJS 
  const ppt = new PptxGenJS();

  // 3. 
  const slide = ppt.addSlide();

  // 4. 
  //  
  slide.addText(messageText, {
    x: 1.0,
    y: 1.5,
    w: 8.0,
    h: 2.0,
    fontSize: 24,
    align: "center",
    color: "363636",
    bold: true,
  });

  // 5. PPTX 
  //  
  ppt.writeFile({ fileName: "myNewSlides.pptx" });
}

/**
 * 
 */
async function clearMessage(callback: () => Promise<void>): Promise<void> {
  const messageElement = document.getElementById("message") as HTMLElement | null;
  if (messageElement) {
    messageElement.innerText = "";
  }
  await callback(); // callbackPromise<void>
}

/**
 * 
 */
function setMessage(message: string): void {
  const messageElement = document.getElementById("message") as HTMLElement | null;
  if (messageElement) {
    messageElement.innerText = message;
  }
}

/**
 *  ()
 */
async function tryCatch(callback: () => Promise<void>): Promise<void> {
  try {
    const messageElement = document.getElementById("message") as HTMLElement | null;
    if (messageElement) {
      messageElement.innerText = "";
    }
    await callback();
  } catch (error: unknown) {
    setMessage("Error: " + String(error));
  }
}
